* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body, button, h1, h2, h3, button, textarea {
    font-family: $base-font;

}
.container {
    max-width: 1170px;
    margin: 0 auto;
    
}
body {
    @include flex(flex-start, stretch, column);
    font-size: $base-font-size;
}